table.keyboard {
  font-size: 14px;
  background: #2F2B39;
  border: none;
  background-image: url(../images/noise.png); }
  table.keyboard tr {
    height: 3em;
    border: none; }
  table.keyboard tr td {
    font-size: 0.8em;
    border: none;
    margin: 0px;
    padding: 0em 0.3em;
    width: 4.5em;
    max-width: 4.5em;
    height: 3em;
    max-height: 3em;
    text-align: center;
    vertical-align: center;
    color: #EBD108; }
  table.keyboard tr td i {
    color: #4BA9F3;
    font-style: normal; }
  table.keyboard tr td b {
    color: #ECF0E4;
    font-weight: normal;
    font-style: italic;
    position: absolute; }
  table.keyboard tr:not(:last-child) td b {
    margin: -18px 29px; }
  table.keyboard tr td span,
  table.keyboard tr td span:active,
  table.keyboard tr td span:focus,
  table.keyboard tr td span:hover {
    font-size: 1.2em;
    display: block;
    text-align: center;
    cursor: pointer;
    color: #ECF0E4;
    font-weight: bold;
    vertical-align: middle;
    -webkit-box-shadow: 1px 1px 3px black, 2px 2px 3px black;
    box-shadow: 1px 1px 3px black, 2px 2px 3px black;
    /*, 3px 3px 3px black;*/
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    border-radius: 3px;
    border: 0px solid #adadad29;
    border-top-width: 1px;
    border-left-width: 1px;
    background-image: url(../images/noise.png); }
  table.keyboard tr td span:focus,
  table.keyboard tr td span:hover {
    -webkit-box-shadow: 1px 1px 3px black;
    box-shadow: 1px 1px 3px black; }
  table.keyboard tr td span.outer {
    padding: 2px;
    border-radius: 4px;
    -webkit-box-shadow: inset 1px 1px 0px black;
    box-shadow: inset 1px 1px 0px black;
    border: none;
    background-color: #272b2e; }
  table.keyboard tr td span.w {
    color: #2F2B39;
    background-color: #EFEFED;
    border-color: #fff; }
  table.keyboard tr td span.f {
    color: #2F2B39;
    background-color: #ddd26c; }
  table.keyboard tr td span.k {
    color: #ECF0E4;
    background-color: #79c0ea; }
  table.keyboard tr td span.r {
    color: #ECF0E4;
    background-color: red; }



